/* 在css文件 */
@import '~antd/dist/antd.css';
*{
    padding: 0;
    margin: 0;
}
ul li{
    list-style-type: none;
}
body{
    background: #ccc;
}
/*  header  */
.header{
    height: 50px;
    background-color: #444;
}
.header header{
    width: 1200px;
    margin: 0 auto;
    line-height: 50px;
}
.header img{
    width: 120px;
}
.header header a{
    margin: 0 20px;
    color: white;
}
/*  content  */
.content{
    width: 1200px;
    margin: 30px auto 0;
}
.left{
    width: 900px;
    height: 500px;
    float: left;
}
.right{
    width: 270px;
    height: 500px;
    float: right;
    background-color: #ccc;

}
.active{
    background: #80bd01;
    color: white;
    border:1px solid #80bd01;
    width: 70px;
    height: 30px;
    border-radius: 5px;
}
.pane1 {
    width: 270px;
    height: 120px;
    font-size: 13px;
    margin-bottom: 13px;
}
.inner {
    padding: 10px;
    border-radius: 0 0 3px 3px;
}
.into {
    display: inline-block;
    float: none;
    padding: 3px 10px;
    border: none;
    margin: 0;
    font-size: 14px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    letter-spacing: 2px;
    box-shadow: none;
    border-radius: 3px;
    line-height: 2em;
    vertical-align: middle;
    color: #fff;
    background-color: #5bc0de;
}
